import React from 'react';
import LoadingContent from "components/global-components/Components/SkeletonLoader/HomePageLoader";
import size from "lodash/size";
import PropTypes from "prop-types";
import { Button, Row } from "reactstrap";
import Box from "..//Box/Box";
import ProductCard from "../ProductCard/ProductCard.cra";

const LoadMore = ({
  handleLoadMore,
  showButton,
  buttonText,
  loading,
  loadMoreComponent,
  loadMoreStyle,
}) => {
  return (
    !!showButton && (
      <Box className="loadmore_wrapper" {...loadMoreStyle}>
        {loadMoreComponent ? (
          loadMoreComponent
        ) : (
          <Button loading={loading} onClick={handleLoadMore}>
            {buttonText || "Load More"}
          </Button>
        )}
      </Box>
    )
  );
};

export default function SectionGrid({
  data = [],
  totalItem,
  limit,
  columnWidth,
  paginationComponent,
  handleLoadMore,
  loadMoreComponent,
  loading,
  buttonText,
  rowStyle,
  columnStyle,
  loadMoreStyle,
  onEdit,
  link,
}) {

  let showButton = data.length < totalItem;

  if (loading) return <LoadingContent />;

  return (
    <>
      <Box className="grid_wrapper" {...rowStyle}>
        {size(data) > 0 &&
          data.map((item) => {
            return (
              <Box
                className="grid_column"
                width={columnWidth}
                key={item._id}
                {...columnStyle}
              >
                <ProductCard
                  onEdit={onEdit}
                  link={link}
                  {...item}
                  columnWidth={columnWidth}
                />
              </Box>
            );
          })}
        {size(data) === 0 && (
          <Row className="flex-column text-center w-100">
            <img
              style={{ height: 250 }}
              className="w-25 align-self-center"
              src={require("../../../../assets/img/setting_listings.svg")}
              alt=""
            />
            <h5>No Data Found</h5>
          </Row>
        )}
      </Box>

      {showButton && (
        <LoadMore
          showButton={showButton}
          handleLoadMore={handleLoadMore}
          loading={loading}
          buttonText={buttonText}
          loadMoreComponent={loadMoreComponent}
          loadMoreStyle={loadMoreStyle}
        />
      )}
      {paginationComponent && (
        <Box className="pagination_wrapper">{paginationComponent}</Box>
      )}
    </>
  );
}

SectionGrid.propTypes = {
  data: PropTypes.array.isRequired,
  totalItem: PropTypes.number,
  columnWidth: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.array,
  ]),
  paginationComponent: PropTypes.element,
  handleLoadMore: PropTypes.func,
  loadMoreComponent: PropTypes.element,
  placeholder: PropTypes.element,
  loading: PropTypes.bool,
  limit: PropTypes.number,
  buttonText: PropTypes.string,
  rowStyle: PropTypes.object,
  columnStyle: PropTypes.object,
  loadMoreStyle: PropTypes.object,
};

SectionGrid.defaultProps = {
  rowStyle: {
    flexBox: true,
    flexWrap: "wrap",
    mr: ["-10px", "-10px", "-10px", "-10px", "-10px", "-15px"],
    ml: ["-10px", "-10px", "-10px", "-10px", "-10px", "-15px"],
  },
  columnStyle: {
    pr: ["10px", "10px", "10px", "10px", "10px", "15px"],
    pl: ["10px", "10px", "10px", "10px", "10px", "15px"],
  },
  loadMoreStyle: {
    flexBox: true,
    justifyContent: "center",
    mt: "1rem",
  },
};
