import React from "react";
import { connect } from "react-redux";
import BaseIndexPage from "../components/HOC/BaseIndexPage";
import OurPartner from "./section-components/our-partner";
import Property from "./section-components/property";
import { getList, get, saveSearch } from "state/Listing/Epic";

const Home_V1 = ({ ...props }) => {
  return (
    <div>
      <Property {...props} />
      <OurPartner />
    </div>
  );
};

const mapStateToProps = (state) => ({
  Header: {
    headertitle: "Renting the way it should be",
    subheader:
      "The destination for finding, advertising, and managing rental property",
  },
});

const mapDispatchToProps = (dispatch) => ({
  get: (params) => dispatch(get(params)),
  getList: (params) => dispatch(getList(params)),
  saveSearch: (params) => dispatch(saveSearch(params)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BaseIndexPage(Home_V1));
